import {
  ChangeDetectionStrategy,
  Component,
  Input,
  OnChanges,
  OnInit
} from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { Changes } from 'ngx-reactivetoolkit';
import { combineLatest, Observable } from 'rxjs';
import { filter, map, startWith } from 'rxjs/operators';
import { FlowStep } from './types/flow-step.type';

@Component({
  selector: 'sof-wizard-nav-bar',
  styleUrls: ['./wizard-nav-bar.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <div class="col">
      <div class="row">
        <a
          *ngFor="let step of steps; let i = index"
          class="col step-wrapper"
          [class.enable-future-steps]="enableFutureSteps"
          [routerLink]="[step.routePath]"
          queryParamsHandling="preserve"
          [class.before-active]="i < (indexActiveStep$ | async)"
          [class.after-active]="i > (indexActiveStep$ | async)"
          [class.active]="i === (indexActiveStep$ | async)"
          [class.step-disabled]="step.disabled"
          [tabindex]="
            i > (indexActiveStep$ | async) && !enableFutureSteps ? -1 : 0
          "
          (click)="blur($event)"
        >
          <div class="row justify-content-center label">
            {{ tc + '.' + step.label | translate }}
          </div>
          <div class="row justify-content-center circle-wrapper">
            <div class="circle"></div>
          </div>
          <div class="row line-wrapper">
            <span class="line"></span>
          </div>
        </a>
      </div>
    </div>
  `
})
export class WizardNavBarComponent implements OnChanges, OnInit {
  @Input() tc: string;
  @Input() steps: FlowStep[];
  @Input() enableFutureSteps = false;

  @Changes('steps') steps$: Observable<FlowStep[]>;

  indexActiveStep$: Observable<number>;
  private triggerCalc$ = this.router.events.pipe(
    filter(event => event instanceof NavigationEnd),
    startWith({})
  );

  constructor(private router: Router) {}

  ngOnInit(): void {
    this.indexActiveStep$ = combineLatest([
      this.triggerCalc$,
      this.steps$
    ]).pipe(map(([triggerCalc, steps]) => this.getIndexActiveStep(steps)));
  }

  ngOnChanges(): void {}

  blur(event: any): void {
    const path = event.path || (event.composedPath && event.composedPath());
    path[0].blur();
  }

  private getIndexActiveStep(flowSteps: FlowStep[]): number {
    for (let i = flowSteps.length; i--; ) {
      const flowStep = flowSteps[i];
      let params;
      if (flowStep.queryParams) {
        params = Object.keys(flowStep.queryParams).reduce(
          (concat, key) =>
            `${concat}&${encodeURIComponent(key)}=${encodeURIComponent(
              flowStep.queryParams[key]
            )}`,
          '?'
        );
      }
      // The route is active if the router returns isActive as true.
      if (
        this.router.isActive(
          `${flowStep.routePath}${params ? params : ''}`,
          false // exact route path match
        )
      ) {
        return i;
      }
    }
    return undefined;
  }
}
